  font-size: .75em;
  display: flex;
}

.todo__action {
  width: 32px;
  height: 32px;
  cursor: pointer;
  opacity: .5;
  background-color: #80deea;
  border-radius: 4px;
  transition: top .3s ease-in;
  position: absolute;
  top: -20px;
  right: 15px;
  transform: translateY(-50%);
}

.todo__action:before {
  width: 20px;
  height: 20px;
  content: "";
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  top: 6px;
  right: 6px;
}

.todo__delete {
  width: 26px;
  height: 26px;
  cursor: pointer;
  background-color: #8bc34a;
  border: #0000;
  transition: background-color .15s ease-in;
  transform: translateY(-30%);
}

.todo__delete:hover {
  background-color: #689f38;
}

.todo__delete:before {
  width: 16px;
  height: 16px;
  content: "";
  background-image: url("https://cdn-icons-png.flaticon.com/512/5680/5680113.png");
